@media all and (min-width: 0px){
    .project{
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        justify-content: space-between;
        gap: 32px;
        position: relative;
    }
    .project-about{
        width: 90%;
    }
    .project-about > *:not(:last-child){
        margin-bottom: 16px;
    }
    .project-about-btn{
        position: absolute;
        top: -50px;
        color: var(--cor-3);   
        font-size: 24px;    
        cursor: pointer;
        transition: transform .5s;
    }
    .project-about-btn:hover{
        transform: scale(1.1);
    }
    .project-about-info{
        display: flex;
        align-items: end;
        gap: 10px;
    }
    .project-about-info-status{
        font-size: 13px;
        color: var(--cor-3);
    }
    .project h4{
        color: var(--cor-2);
    }
    .project-about-links{
        width: 100%;
        display: flex;
        flex-wrap: wrap;
    }
    .project-link-img{
        width: 90%;
    }
    .project-img{
        width: 100%;
    }
}

@media all and (min-width: 800px){
    .project{
        width: 95%;
        flex-direction: row;
    }
    .project-about{
        width: 50%;
    }
    .project-link-img{
        width: 50%;
        height: 250px;
    }
}